			float: left;
			margin-right: 15px;
			min-height: 285px;
		}

		.dropdown-menu {
			display: block;
			position: static;
			top: auto;
			z-index: 0;
		}

		.dropdown-menu-horizontal {
			display: flex;
		}

		.dropdown-wide-container {
			float: left;

			.dropdown-menu {
				display: flex;
			}
		}

		.mega-menu .dropdown-menu {
			float: left;
			max-width: 100%;
		}
