	.container-fluid .clay-site-grid-width {
		display: block;
	}

	.container-fluid-max-sm .clay-site-grid-width-sm,
	.container-fluid-max-md .clay-site-grid-width-md,
	.container-fluid-max-lg .clay-site-grid-width-lg,
	.container-fluid-max-xl .clay-site-grid-width-xl,
	.container-fluid-max-xxl .clay-site-grid-width-xxl {
		display: inline;
	}

	@each $breakpoint, $value in $container-max-widths {
		.container-fluid-max-#{$breakpoint}
			.clay-site-grid-width-#{$breakpoint} {
			&:after {
				content: ' #{$value}';
			}
		}

		[data-container-classes='container-fluid container-fluid-max-#{$breakpoint} main-nav-container'] {
			&:after {
