	// Popovers Page

	.clay-site-popover-display-column {
		display: inline-block;
		height: 240px;
		position: relative;
		width: 250px;
	}

	// Progress Bar Page

	.clay-site-progress-bar-page {
		.progress {
			margin-bottom: 15px;
		}

		.progress-group {
			margin-bottom: 15px;

			.progress {
				margin-bottom: 0;
			}
		}
	}

	// Stickers

	.clay-site-positional-sticker {
		max-width: 300px;
	}

	// Tables Page

	#verticalAlignTableListDemo {
		background-color: #f5f7f8;

		tr {
			height: 120px;
		}
	}

	// Tooltips Page

	.clay-site-tooltip-display-column {
		display: inline-block;
		height: 100px;
		position: relative;
		width: 250px;
	}

	// Typography Page

	.clay-site-sans-serif {
		font-family: $font-family-sans-serif;
	}
	.clay-site-sans-serif::after {
		content: '#{$font-family-sans-serif}';
	}

	.clay-site-serif {
		font-family: $font-family-serif;
	}
	.clay-site-serif::after {
		content: '#{$font-family-serif}';
	}

	.clay-site-monospace {
		font-family: $font-family-monospace;
	}
	.clay-site-monospace::after {
		content: '#{$font-family-monospace}';
	}

	// Site Main Nav

	.side-navigation-data-api-buttons {
		margin-bottom: 15px;

		.btn {
			margin-bottom: 3px;
		}
	}

	.side-navigation-methods {
		h4 {
			margin-top: 2em;

			&:nth-of-type(1) {
				margin-top: 1em;
			}
		}
	}

	.menu-toggle {
		color: #fff;
		display: block;
		float: left;
		padding: 10px 15px;
		text-align: left;

		.glyphicon {
			font-size: 22px;
		}
	}

	// Main Nav Structure

	$mainNavWidth: 220px;

	.lexicon-site-body-fixed {
		bottom: 0;
		left: 0;
		position: fixed;
		right: 0;
		top: 0;

		@include media-breakpoint-up(md) {
			// min-width 768px
			position: static;
		}
	}

	#mainContent {
		left: 0;
		position: relative;

		@include media-breakpoint-up(md) {
			// min-width 768px
			padding-left: $mainNavWidth + 20;
		}

		[dir='rtl'] & {
			@include media-breakpoint-up(md) {
				// min-width 768px
				padding-left: 0;
				padding-right: $mainNavWidth + 20;
			}
		}
	}

	#mainNav {
		overflow: hidden;
		position: absolute;
		visibility: hidden;
		width: 0;

		@include media-breakpoint-up(md) {
			// min-width 768px
			visibility: visible;
			width: $mainNavWidth;
		}
	}

	.main-nav-container {
		position: relative;
	}

	.main-nav-inner {
		padding: 3px;
		width: $mainNavWidth;
	}

	.main-nav-open {
		-webkit-overflow-scrolling: touch;
		height: 100%;
		overflow-x: hidden;

		@include media-breakpoint-up(md) {
			// min-width 768px
			overflow-x: visible;
		}

		#mainNav {
			visibility: visible;
			width: $mainNavWidth;
		}

		#mainContent {
			left: $mainNavWidth + 10;

			@include media-breakpoint-up(md) {
				// min-width 768px
				left: auto;
				position: static;
			}
		}
	}

	.main-nav-transition {
		#mainNav {
			visibility: visible;
		}

		#mainNav,
		#mainContent {
			transition: all 0.25s ease;
		}
	}

	// Main Nav Skin

	#mainNav {
		padding-top: 1em;
		z-index: 1;

		li {
			> a {
				&:hover,
				&:focus {
					background-color: transparent;
					text-decoration: underline;
				}
			}

			&.child-active,
			&.active {
				> a {
					color: #0058ba;
					font-weight: bold;
				}
			}
		}

		.main-nav-section li {
			.nav-title {
				display: block;
				padding: 10px 15px;
			}

			> h4 {
				color: #f3f3f3;
				padding: 10px 15px;
			}

			div > .main-nav-section {
				border-left: 3px solid #e4e4e4;
				margin-left: 15px;

				li {
					> a {
						font-size: 0.9em;
						padding: 2px;
						padding-left: 10px;
					}

					&.child-active,
					&.active {
						> a {
							border-left: 3px solid #0058ba;
							margin-left: -3px;
						}
					}
				}
			}

			&.nav-static-link > .nav-title {
				padding-top: 0;
			}
		}
	}

	// Metalsmith Headings Identifiers

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		.heading-anchor {
			align-items: center;
			color: inherit;
			cursor: pointer;
			display: inline-flex;
			font-size: 14px;
			height: 32px;
			justify-content: center;
			line-height: 1;
			position: relative;
			text-decoration: none;
			vertical-align: middle;
			width: 32px;
			z-index: 1;

			.lexicon-icon {
				margin-top: 0;
			}
		}

		@include media-breakpoint-up(md) {
			// min-width 768px
			.heading-anchor {
				margin-left: -32px;
				opacity: 0;

				&:focus {
					opacity: 1;
				}
			}

			&:hover .heading-anchor {
				opacity: 1;
			}
		}
	}

	// Side Navigation Component Page

	body {
		left: 0;
		position: relative;
	}

	body.open {
		overflow: hidden;

		@media (min-width: 768px) {
			left: 320px;
		}
	}

	body.sidenav-transition {
		overflow: hidden;
	}

	#mySidebar {
		background-color: #222328;
		box-shadow: -3px 0 3px -3px #000 inset;
		color: #f8f8f8;

		ul {
			border-bottom: 1px solid #3e3e3e;
			border-top: 1px solid #000;

			li {
				border-bottom: 1px solid #000;
				border-top: 1px solid #3e3e3e;
				margin-top: 0;

				a {
					background-color: #2c2d32;
					box-shadow: -3px 0 3px -3px #000 inset;

					&,
					&:hover,
					&:focus {
						color: #b3b6bc;
						font-weight: 200;
						text-shadow: 0 1px 0 #000;
					}
				}
			}
		}
	}

	#mySidebar h3 {
		margin: 0;
		padding: 15px;
	}

	#sidenavDemo {
		margin-top: 20px;

		h4 {
			border-bottom: 2px dotted #aeaeae;
			color: #ba3a37;
			margin: 20px 0;
			padding-bottom: 8px;
		}

		.sidenav-main-content h3 {
			border-bottom: 1px solid #1e2639;
			color: #f8f8f8;
			margin: 0 -20px;
			padding: 15px;
		}
	}

	.sidenav-demo-container {
		overflow: hidden;
		padding-top: 3px;
	}

	// Side Navigation RTL

	[dir='rtl'] {
		.sidenav-fixed > .sidenav-menu-slider {
			left: auto;
			right: 0;
		}

		.sidenav-fixed.sidenav-right > .sidenav-menu-slider {
			left: 0;
			right: auto;
		}

		.sidenav-container.closed > .sidenav-content {
			right: 0;
		}

		.sidenav-container.sidenav-right {
			.sidenav-content {
				left: 0;
				right: auto;
			}

			.sidenav-menu {
				left: 0;
				right: auto;
			}

			.sidenav-menu-slider {
				left: 0;
				right: auto;
			}
		}

		.sidenav-fixed.sidenav-menu-slider.sidenav-right {
			left: 0;
			right: auto;
		}
	}
}

// API Page

$my-button-variant: () !default;
$my-button-variant: map-merge(
	(
		bg: #c142a0,
		border-width: 0,
		color: #fff,
		font-size: 13px,
		font-weight: 700,
		line-height: 1,
		padding-bottom: 12px,
		padding-left: 15px,
		padding-right: 15px,
		padding-top: 12px,
		hover-bg: #ac388e,
		hover-color: #fff,
		active-bg: #9d3381,
	),
	$my-button-variant
);

.my-button-variant {
	@include clay-button-variant($my-button-variant);
}
