.loading-animation {
	display: block;
	font-size: $loading-icon-font-size;
	height: 1em;
	margin-left: auto;
	margin-right: auto;
	position: relative;
	text-align: left;
	vertical-align: middle;
	width: 1em;
}

// Loading Icon Dotted

%loading-animation {
	@include clay-loading-animation-variant($loading-animation);
}

%loading-animation-light {
	@include clay-loading-animation-variant($loading-animation-light);
}

.loading-animation {
	@extend %loading-animation !optional;
}

.loading-animation-light {
	@extend %loading-animation-light !optional;
}

// Loading Icon Sizes

.loading-animation.loading-animation-sm {
	font-size: $loading-icon-font-size-sm;
}

.lfr-ani-2 {
	display: block;
	font-size: 64px;
	height: 1em;
	position: relative;
	text-align: left;
	vertical-align: middle;
	width: 1em;

	background:rgba(0,0,0,0.1);

	&::after {
		color: #6b6c7e;
		content: "";
		display: block;
		font-size: 0;
		height: 0px;
		left: 22px;
		position: absolute;
		top: 22px;
		width: 0px;
		box-shadow: -0.1875em -0.1875em 0 0.15625em #80acff, 0.34375em 0.34375em 0 0.3125em #0b5eff;
		background: #000;
	}
}

.lfr-ani-1 {
	--lfr-ani-1-animation: 2.35s ease-out infinite;
	display: block;
	font-size: 1em;
	height: 1em;
	position: relative;
	width: 1em;

	&::before {
		animation: var(--lfr-ani-1-animation);
		animation-name: lfr-ani-1-box-1;
		background-color: $primary-l1;
		border-radius: 0.2em;
		content: '';
		display: block;
		font-size: 0.3125em;
		height: 1em;
		left: 0;
		position: absolute;
		top: 0;
		transform: scale(1);
		width: 1em;
	}

	&::after {
		animation: var(--lfr-ani-1-animation);
		animation-name: lfr-ani-1-box-2;
		background-color: #0b5fff;
		border-radius: 0.2em;
		content: '';
		display: block;
		font-size: 0.3125em;
		height: 1em;
		left: calc(100% - 1.5em);
		position: absolute;
		top: calc(100% - 1.5em);
		transform: scale(2);
		width: 1em;
	}
}

.lfr-ani-1-lg {
	font-size: 4rem;
}

@keyframes lfr-ani-1-box-1 {
	0% {
		left: 0;
		top: 0;
		transform: scale(1);
	}
	25% {
		left: calc(100% - 1em);
		top: 0;
		transform: scale(1);
	}
	50% {
		left: calc(100% - 1.5em);
		top: calc(100% - 1.5em);
		transform: scale(2);
	}
	75% {
		left: calc(1em / 2);
		top: calc(100% - 1.5em);
		transform: scale(2);
	}
}

@keyframes lfr-ani-1-box-2 {
	0% {
		left: calc(100% - 1.5em);
		top: calc(100% - 1.5em);
		transform: scale(2);
	}
	25% {
		left: calc(1em / 2);
		top: calc(100% - 1.5em);
		transform: scale(2);
	}
	50% {
		left: 0;
		top: 0;
		transform: scale(1);
	}
	75% {
		left: calc(100% - 1em);
		top: 0;
		transform: scale(1);
	}
}